import "../buttons";
import "../text_field";

import "./style.scss";

export default function loginFormInit() {
  const formEl = document.querySelector(".login-form__form");

  const submitFormHandler = (e) => {
    e.stopPropagation();
    e.preventDefault();

    console.log("login form submit");
  }

  formEl.addEventListener("submit", submitFormHandler);
}

loginFormInit();